@import "./components/components.fonts";
@import "./components/components.mixins";
@import "./components/components.general";
@import "./components/components.header";
@import "./components/components.footer";

* {
  margin: 0;
  padding: 0;
  outline: none;
  text-decoration: none;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  border-collapse: collapse;
}

img {
  user-select: none;
  -moz-user-select: none;
  -ms-user-select: none !important;
  -webkit-user-select: none !important;
}

a {
  color: inherit;
  cursor: pointer;
}
a:focus {
  outline: none;
}
button {
  cursor: pointer;
  border: none;
}

.container {
  max-width: 1920px;
  padding: 50px 100px 50px 350px;
  margin: 0 auto;
  // outline: 1px solid red;
}

$blue: #139cff;
$light-blue: #e3eeff;
$medium-blue: #4f5468;
$dark-blue: #2c3144;
$dark-grey: #4f5468;
$medium-grey: #5c6178;
$light-grey: #747c98;

body {
  font-family: $font;
  font-size: 18px;
  line-height: 130%;
  font-weight: 400;
  background: $dark-grey;
  color: $dark-blue;
}

.overflow {
  position: relative;
  overflow: hidden;
  outline: 1px solid white;
  margin: 0 auto;
}
input,
select,
textarea,
a,
button {
  font-family: $font;
}

select {
  font-family: $font, sans-serif !important;
}

//НАЧАЛО - СЕКЦИЯ header *****************************************************

header {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 50px;
  background: $medium-grey;
}
.header__container {
  padding: 0px 100px 0px 350px;
  max-width: 1920px;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
}
.header__container_mobile {
  display: none;
}
.search {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.search__btn {
  border: none;
  width: 84px;
  height: 50px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background: none;
  color: white;
  transition: all ease-out 0.3s;
}
.search__btn:hover {
  background: $blue;
}
.search__btn_active {
  background: $blue;
}
.search__input {
  flex-grow: 1;
  height: 50px;
  padding: 15px 30px;
  background: $light-grey;
  font-weight: normal;
  font-size: 16px;
  line-height: 20px;
  color: white;
  border: none;
  transition: all ease-out 0.3s;
  pointer-events: none;
  opacity: 0;
}
.search__input_active {
  pointer-events: auto;
  opacity: 1;
}
::-webkit-input-placeholder {
  color: #ffffff;
}
:-moz-placeholder {
  color: #ffffff;
}
::-moz-placeholder {
  color: #ffffff;
}
:-ms-input-placeholder {
  color: #ffffff;
}
.city__btn {
  border: none;
  width: 130px;
  height: 50px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background: none;
  color: white;
  transition: all ease-out 0.3s;
  img {
    margin-left: 10px;
    transition: all ease-out 0.3s;
  }
}
.city__btn_active {
  pointer-events: none;
  background: $light-grey;
  img {
    transform: rotate(180deg);
  }
}
.city {
  position: relative;
}
.city__input {
  height: 30px;
  width: 225px;
  padding: 5px 25px;
  background: $light-grey;
  font-weight: normal;
  font-size: 16px;
  line-height: 20px;
  color: white;
  border: none;
  transition: all ease-out 0.3s;
  pointer-events: none;
  opacity: 0;
  position: absolute;
  right: 0;
  bottom: -30px;
}
.city__input_active {
  pointer-events: auto;
  opacity: 1;
}
.language__btn {
  border: none;
  width: 80px;
  height: 50px;
  padding: 5px 15px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background: none;
  color: white;
  transition: all ease-out 0.3s;
  position: relative;
  img {
    margin-left: 10px;
    transition: all ease-out 0.3s;
  }
}

.language__btn::after {
  content: url(../img/arrow-bottom.svg);
  display: block;
  width: 15px;
  height: 15px;
  z-index: 999;
  position: absolute;
  top: 0;
  right: 0;
}
select {
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  appearance: none;
  background: url("../img/arrow-bottom.svg") no-repeat 93% 50%;
}
select::-ms-expand {
  display: none;
}
.language__btn:focus {
  pointer-events: none;
  background: $light-grey;
  img {
    transform: rotate(180deg);
  }
}
.language__item {
  width: 80px;
  height: 30px;
  padding: 5px 15px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background: $light-grey;
  color: white;
}
option {
  padding: 5px 15px;
}
option[selected] {
  background: $blue;
  color: white !important;
}

//НАЧАЛО - menu *****************************************************
.menu {
  width: 250px;
  font-size: 16px;
  line-height: 20px;
  color: white;
  background: $medium-grey;
  position: fixed;
  top: 0;
  left: calc(50% - 960px);
}
.logo {
  background: $blue;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 30px 45px;
  img {
    width: 158px;
    height: 38px;
  }
}
.menu__list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-bottom: 50px;
  background: $dark-grey;
  padding-top: 1px;
  padding-bottom: 1px;
}
.menu__item {
  padding: 15px 10px 15px 50px;
  // border-top: 2px solid $dark-grey;
  // border-bottom: 2px solid $dark-grey;
  margin-top: 1px;
  margin-bottom: 1px;
  background: $medium-grey;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  transition: all ease-out 0.3s;
}
.menu__item:hover {
  background: $light-grey;
}
.menu__item_active {
  background: $light-grey;
  border-right: 10px solid $blue;
}
.social-networks {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-bottom: 165px;
}
.social-link {
  display: inline-block;
  margin: 0px 15px;
  transition: all ease-out 0.2s;
  img {
    max-width: 15px;
  }
}
.social-link:hover {
  transform: scale(1.7);
  filter: sepia(100%) hue-rotate(165deg) saturate(400%);
}
.about__list {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 30px;
}
.about__link {
  display: inline-block;
  font-size: 14px;
  line-height: 130%;
  text-decoration-line: underline;
  color: #ffffff;
  width: 100%;
  padding: 10px 10px 10px 50px;
  transition: all ease-out 0.3s;
}
.about__link:hover {
  color: $blue;
}
.about__link_active {
  color: $blue;
  border-right: 10px solid $blue;
}
.menu__year {
  font-size: 16px;
  line-height: 20px;
  padding-left: 50px;
}

//НАЧАЛО - slider-wrapper  *****************************************************

.slider-wrapper {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  position: relative;
  margin-top: 50px;
}
.main-slider {
  width: 65%;
  max-width: 940px;
}
.main__wrapper-img {
  width: 100%;
  height: 540px;
  overflow: hidden;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% 50%;
}
// .main__slide_img {
//   width: 100%;
//   height: 100%;
//   object-fit: cover;
// }
.main__slide-link {
  display: inline-block;
  background: white;
  width: 80%;
  max-width: 700px;
  padding: 35px 50px;
  margin-top: -150px;
  position: relative;
  z-index: 10;
}
.main__slide-title {
  width: 100%;
  font-weight: 600;
  font-size: 30px;
  line-height: 165%;
  color: $dark-blue;
  margin-bottom: 20px;
}
.main__slide-text {
  width: 100%;
  font-weight: normal;
  font-size: 16px;
  line-height: 20px;
  color: $medium-blue;
}
.main__slide_section {
  color: $light-grey;
}
.main__slide_slash {
  color: $light-grey;
}

.toggle-slider {
  width: 35%;
}
.toggle__slide {
  width: 100%;
  max-width: 450px;
  padding: 5px 20px;
  color: white;
  background: $light-grey;
  margin-bottom: 10px;
  cursor: pointer;
  transition: all ease-out 0.3s;
  display: flex !important;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}
.toggle__title_start {
  font-size: 16px;
  line-height: 24px;
  max-height: 48px;
  overflow: hidden;
}
.toggle__slide_active {
  max-width: 500px;
  font-weight: 600;
  color: $dark-blue;
  background: #ffffff;
  border-left: 10px solid $blue !important;
}

.toggle-slider .slick-slide {
  float: right;
}
.toggle-slider_start .slick-slide {
  height: 100px;
}
.slick-dots {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  position: absolute;
  right: 100px;
  bottom: 0px;
  pointer-events: none;
}
.slider__dot {
  width: 50px;
  height: 2px;
  background: $light-grey;
  transition: all ease-out 0.3s;
}
.slick-dots .slick-active .slider__dot {
  height: 10px;
  background: $blue;
}
.slider__dot_abs {
  display: none;
}
.slick-active .slider__dot .slider__dot_abs {
  display: block;
  font-weight: bold;
  font-size: 28px;
  line-height: 30px;
  color: $light-grey;
  position: absolute;
  right: -70px;
  bottom: -7px;
}
.section__start .slick-dots {
  display: none !important;
}

//НАЧАЛО - main-content  *****************************************************
.container__main {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
}
.container__main_end {
  align-items: flex-end;
  padding-top: 0;
}
.sidebar {
  width: 20%;
  max-width: 300px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  overflow: hidden;
}
.ad-unit_big {
  max-width: 300px;
  max-height: 600px;
  overflow: hidden;
}
.ad-unit_small {
  max-width: 300px;
  max-height: 250px;
  overflow: hidden;
}
.ad-unit_bottom {
  width: 100%;
}
@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .ad-unit_bottom {
    width: 100%;
    height: 100%;
  }
}
.ad-unit_margin {
  margin-bottom: 50px;
}
.main-content {
  width: 75%;
  max-width: 1090px;
}
.section-card {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}
.section-card_margin-bottom {
  margin-bottom: 100px;
}
.section-name__wrapper {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: 50px;
}
.section-card__wrapper {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: stretch;
  flex-wrap: nowrap;
}
.section-title {
  font-weight: bold;
  font-size: 48px;
  line-height: 59px;
  letter-spacing: 0.1em;
  color: #ffffff;
  margin-right: 30px;
  text-transform: uppercase;
}
.section-link {
  font-weight: bold;
  font-size: 18px;
  line-height: 22px;
  letter-spacing: 0.1em;
  color: $light-grey;
}
.card {
  width: 31%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  overflow: hidden;
  background: white;
  position: relative;
}
.card:hover .card-title {
  color: $blue;
}
.card:hover .card-img {
  transform: scale(1.05);
}
.card-wrapper {
  width: 100%;
  height: 250px;
  overflow: hidden;
  position: relative;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% 50%;
}
// .card-img {
//   width: 100%;
//   height: 100%;
//   object-fit: cover;
//   transition: all ease-out 0.3s;
// }
.card-title {
  width: 100%;
  font-weight: 600;
  font-size: 20px;
  line-height: 150%;
  color: $dark-blue;
  padding: 20px 20px 63px 20px;
  transition: all ease-out 0.3s;
}
.card-data {
  width: 100%;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  color: $dark-grey;
  position: absolute;
  left: 20px;
  bottom: 20px;
}

//НАЧАЛО - main-content страницы рубрики *****************************************************

.articles {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 75%;
  max-width: 1090px;
}
.articles__wrapper {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: stretch;
  flex-wrap: wrap;
}
.section__container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  padding-top: 0px;
}
.articles .card {
  margin-bottom: 50px;
}
.articles__pagination {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-bottom: 100px;
}
.articles__pagination-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.articles__pagination_btn {
  width: 30px;
  height: 30px;
  color: white;
  background: $blue;
  border: none;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  transition: all ease-out 0.3s;
  margin: 5px;
}
.articles__pagination_btn:hover {
  transform: scale(1.1);
}
.articles__pagination_page {
  width: 30px;
  height: 30px;
  color: white;
  background: none;
  border: 1px solid white;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  transition: all ease-out 0.3s;
  margin: 5px;
  cursor: pointer;
}
.articles__pagination_active {
  background: $blue;
}
.articles__pagination_page:hover {
  background: $blue;
}

//НАЧАЛО - main-content СТАТЬИ *****************************************************

.article__container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  padding-top: 100px;
}
.article {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  width: 75%;
  max-width: 1090px;
}
.article__wrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}
.article__img {
  width: 100%;
  margin-bottom: 30px;
}
@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .article__img {
    width: 100%;
    height: 100%;
  }
}
.article__title {
  width: 100%;
  font-weight: 600;
  font-size: 30px;
  line-height: 165%;
  color: #ffffff;
  margin-bottom: 20px;
}
.article__text {
  width: 100%;
  font-weight: normal;
  font-size: 18px;
  line-height: 165%;
  color: #ffffff;
  margin-bottom: 30px;
}
.article__ad {
  display: none;
}
.article__link {
  color: #88cdff;
  font-weight: 600;
}
.article__info {
  width: 100%;
  margin-bottom: 50px;
  color: white;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  font-weight: normal;
  font-size: 14px;
  line-height: 200%;
}
.share-article {
  width: 250px;
  height: 50px;
  background: $blue;
  color: white;
  font-size: 16px;
  line-height: 20px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  transition: all ease-out 0.3s;
}
.share-article:hover {
  transform: scale(1.05);
}
.section-card_margin-top {
  margin-top: 50px;
}

//НАЧАЛО - страница politics-confidentiality **************************************************

.article__subtitle {
  width: 100%;
  color: #88cdff;
  font-weight: 600;
  margin-bottom: 30px;
}

//НАЧАЛО - страница advertising **************************************************

.advertising {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  width: 100%;
  max-width: 1090px;
}
.advertising__list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  font-weight: normal;
  font-size: 18px;
  line-height: 165%;
  color: #ffffff;
  margin-bottom: 100px;
}
.advertising__item {
  margin-bottom: 7px;
}
.feedback {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
  margin-top: 15px;
}
.feedback__input {
  width: 300px;
  height: 50px;
  color: white;
  background: $light-grey;
  font-weight: normal;
  font-size: 16px;
  line-height: 20px;
  border: none;
  padding: 15px 30px;
  margin-bottom: 20px;
  margin-right: 20px;
}
.feedback__text {
  width: 600px;
  height: 190px;
  color: white;
  background: $light-grey;
  font-weight: normal;
  font-size: 16px;
  line-height: 20px;
  border: none;
  padding: 15px 30px;
  margin-bottom: 20px;
  float: right;
}
.submite__wrapper {
  width: 100%;
  margin-top: 30px;
}
.feedback__btn {
  width: 250px;
  height: 50px;
  background: $blue;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-weight: normal;
  font-size: 16px;
  line-height: 20px;
  color: #ffffff;
  transition: all ease-out 0.3s;
}
.feedback__btn:hover {
  transform: scale(1.05);
}
.wrapper-captcha {
  width: 100%;
  overflow: hidden;
}

//НАЧАЛО - СЕКЦИЯ economy *****************************************************

.economy {
  background: $light-blue;
}
.section-title_dark {
  color: $dark-blue;
}

//НАЧАЛО - СЕКЦИЯ recommend *****************************************************

.recommend {
  background: $light-blue;
}
.recommend__container {
  padding-top: 10px !important;
}
.main-slider_recommend {
  max-width: 860px;
  width: 60%;
}
.toggle-slider_recommend {
  width: 38%;
  max-width: 580px;
}
.main-slider_recommend .main__slide {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-end;
}
.toggle-slider_recommend .toggle__slide {
  max-width: 580px;
}
.toggle-slider_recommend .toggle__slide_active {
  border: none !important;
}
.toggle-slider_recommend .toggle__slide {
  padding: 0px;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 30px;
  height: 150px;
}
.toggle-slider_recommend .toggle__wrapper {
  width: 35%;
  max-width: 200px;
  height: 150px;
  overflow: hidden;
  float: left;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% 50%;
}
// .toggle-slider_recommend .toggle__img {
//   width: auto;
//   height: 100%;
// }
.toggle-slider_recommend .toggle__title {
  width: 65%;
  padding: 0px 20px;
  font-size: 16px;
  line-height: 24px;
  max-height: 68px;
  overflow: hidden;
}

//НАЧАЛО - СЕКЦИЯ media main page *****************************************************

@media (max-width: 1920px) {
  .menu {
    top: 0;
    left: 0;
  }
  .main-slider {
    width: 60%;
  }
}
@media (max-width: 1680px) {
  .toggle__slide {
    width: 100% !important;
    max-width: none;
  }
  .toggle__slide_active {
    width: 100% !important;
    max-width: none;
  }
  .main__slide-link {
    width: 85%;
  }
}
@media (max-width: 1440px) {
  .container {
    padding: 50px 70px 50px 320px;
  }
  .header__container {
    padding: 0px 70px 0px 320px;
  }
  .card-title {
    font-size: 18px;
  }
  .section-title {
    margin-bottom: 15px;
  }
  .section-card_margin-bottom {
    margin-bottom: 70px;
  }
}
@media (max-width: 1366px) {
  .section-card__wrapper {
    overflow: hidden;
  }
  .card {
    width: 48%;
  }
  .card_hidden {
    display: none;
  }
  .main-content {
