  .search,
  .city,
  .language {
    display: none;
  }
  .menu__wrapper {
    display: block;
    padding-bottom: 100px;
  }
  .menu {
    left: -250px;
    transition: all ease-out 0.3s;
  }
  .menu_open {
    left: 0;
  }
  .header__container {
    display: none;
  }
  .header__container_mobile {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0px 50px;
    border-bottom: 10px solid $blue;
  }
  .btn-menu {
    width: 40px;
    height: 30px;
    position: relative;
  }
  .btn-menu .line {
    background: white;
    width: 100%;
    height: 3px;
    transition: all ease-out 0.3s;
    pointer-events: none;
  }
  .first__line {
    position: absolute;
    left: 0;
    top: 0;
  }
  .second__line {
    position: absolute;
    left: 0;
    top: calc(50% - 2px);
  }
  .third__line {
    position: absolute;
    left: 0;
    bottom: 0;
  }
  .btn-menu_open {
    .first__line {
      top: calc(50% - 2px);
      transform: rotate(45deg);
    }
    .second__line {
      opacity: 0;
      transform: rotate(45deg);
      transition: all ease-out 0s;
    }
    .third__line {
      top: calc(50% - 2px);
      transform: rotate(-45deg);
    }
  }
  .scroll-hidden {
    overflow: hidden;
  }
  .main__slide-link {
    width: 90%;
    margin-left: 5%;
    padding: 25px 35px;
    margin-top: -100px;
